"use client";

import React, { useState, FormEvent } from "react";
import Image from "next/image";
import { useRouter } from "next/navigation";
import axios from "axios";
import Eye from "../../../../public/assets/Eye.png";

import google from "../../../../public/assets/google.png";
import image1 from "../../../../public/assets/Accept tasks-amico 1.png";
import Link from "next/link";
import "../../../../src/app/globals.css";
import { toast } from "react-toastify";
import Toast from "../../../components/auth/Toast";
import headerImage from "../../../../public/assets/Logo Icon.png";
import { authService } from "../../../lib/api/services";
import { GoogleLogin } from "@react-oauth/google";
import hidden from '../../../../public/assets/hidden.png'
import GoogleSignIn from "@/customHooks/useGoogleSignup";
interface SignUpFormData {
  name: string;
  email: string;
  organisation: string;
  password: string;
}

export default function SignUp() {
  const router = useRouter();
  const [formData, setFormData] = useState<SignUpFormData>({
    name: "",
    email: "",
    organisation: "",
    password: "",
  });


  const [passwordValidation, setPasswordValidation] = useState({
    hasUpperCase: false,
    hasLowerCase: false,
    hasNumber: false,
    hasSpecialChar: false,
    isLongEnough: false,
  });
  const [isLoading, setIsLoading] = useState(false);
  const [visibility, setVisibility] = useState(true);
  const [passwordVisible, setPasswordVisible] = useState(false);

  const validatePassword = (password: string) => {
    setPasswordValidation({
      hasUpperCase: /[A-Z]/.test(password),
      hasLowerCase: /[a-z]/.test(password),
      hasNumber: /\d/.test(password),
      hasSpecialChar: /[!@#$%^&*(),.?":{}|<>]/.test(password),
      isLongEnough: password.length >= 12,
    });
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));

    if (name === "password") {
      validatePassword(value);
    }
  };

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();

    if (!formData.name) {
      toast.error("Name is required");
      return;
    }
    if (!formData.email) {
      toast.error("Email is required");
      return;
    }
    if (!formData.organisation) {
      toast.error("Organisation name is required");
      return;
    }
    if (!formData.password) {
      toast.error("Password is required");
      return;
    }

    const {
      isLongEnough,
      hasUpperCase,
      hasLowerCase,
      hasNumber,
      hasSpecialChar,
    } = passwordValidation;
    if (
      !isLongEnough ||
      !hasUpperCase ||
      !hasLowerCase ||
      !hasNumber ||
      !hasSpecialChar
    ) {
      toast.error("Please ensure your password meets all requirements");
      return;
    }

    setIsLoading(true);

    try {
      const response = await authService.signup({
        fullName: formData.name,
        email: formData.email,
        password: formData.password,
        organization: formData.organisation,
      });

      if (response && response.status === 201) {
        localStorage.setItem("token", response.data.token);
        toast.success("Account created successfully! Redirecting to login...");
        setTimeout(() => {
          router.push("/auth/login");
        }, 1000);
      }
    } catch (error) {
      if (axios.isAxiosError(error)) {
        if (error.response?.status === 409) {
          toast.error("An account with this email already exists");
        } else {
          toast.error("An error occurred during signup");
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  

  return (
    <>
      <Toast />
      <div className="h-screen w-screen flex overflow-hidden ">
        {/* Left Section */}
        <div className="w-1/2 h-full bg-white flex items-center justify-center overflow-y-auto ">
          <div className="w-full  px-6 py-8 flex flex-col h-full scale-[0.9] origin-top-left">
            {/* Header */}
            <div className="flex items-center justify-center mb-4 ml-20 mt-4 ">
              <Image src={headerImage} alt="Logo" className="w-11 h-auto" />
              <span className="ml-2  text-3xl font-rosario font-bold text-[#333333]">
                <span className="font-rosario text-[#333333] font-bold text-4xl">B</span>lackcofferToDos
              </span>
            </div>

            {/* Title */}
            <div className="text-center mb-4 w-full ml-7 ">
              <h1 className="text-2xl font-semibold font-sans mt-7  flex-grow whitespace-nowrap">
                Try <span className="text-[#5D56BD] italic font-sans font-md">BlackcofferToDos</span>{" "}
                for free
              </h1>
              <p className="text-md font-regular text-[#333333] mt-1">
                No credit card required. Cancel anytime.
              </p>
            </div>

            {/* Form */}
            <form
              onSubmit={handleSubmit}
              className="flex-grow flex flex-col space-y-4 mt-10 ml-28"
            >
              <div className="flex flex-col w-full">
                <label className="text-[#000000] text-sm font-regular mb-1">
                  Full name
                </label>
                <input
                  type="text"
                  name="name"
                  value={formData.name}
                  onChange={handleChange}
                  className="focus:outline-none focus:ring-0 pl-2.5 pr-20 py-3 border border-[#B3B3B3] text-[#B3B3B3] font-light rounded-lg text-medium"
                  placeholder="Enter your full name"
                />
              </div>

              <div className="flex flex-col w-full">
                <label className="text-[#000000] text-sm font-regular mb-1">Email</label>
                <input
                  type="email"
                  name="email"
                  value={formData.email}
                  onChange={handleChange}
                  className="focus:outline-none focus:ring-0  pl-2.5 pr-20 py-3 border border-[#B3B3B3] text-[#B3B3B3] font-light rounded-lg text-medium"
                  placeholder="Enter your email"
                />
              </div>

              <div className="flex flex-col w-full">
                <label className="text-[#000000] text-sm font-regular mb-1">
                  Organisation's name
                </label>

                <input
                  type="text"
                  name="organisation"
                  value={formData.organisation}
                  onChange={handleChange}
                  className="focus:outline-none border rounded-lg border-[#B3B3B3]  focus:ring-0 pl-2.5 pr-20 py-3  text-[#B3B3B3] font-light text-medium"
                  placeholder="Enter organisation name"
                />

              </div>

              <div className="flex flex-col w-full">
                <label className="text-[#000000] text-sm font-regular mb-1">
                  Create Password
                </label>
                <div className="relative border border-[#BDBDBD] rounded-lg">
                  <input
                    type={passwordVisible ? "text" : "password"}
                    name="password"
                    value={formData.password}
                    onChange={handleChange}
                    onClick={() => setVisibility(false)}
                    className="focus:outline-none focus:ring-0 focus:border-transparent pl-2.5 pr-20 py-3 text-[#B3B3B3] font-light rounded-lg text-medium"
                    placeholder="Create new Password"
                  />

                  <button
                    type="button"
                    onClick={() => setPasswordVisible(!passwordVisible)}
                    className="absolute top-3 right-2"
                  >

                    <Image
                      src={passwordVisible ? Eye : hidden}
                      alt={passwordVisible ? "Hide password" : "Show password"}
                      width={24}
                      height={24}
                    />
                  </button>

                </div>

                {/* Password Requirements */}
                {!visibility && (
                  <ul className="mt-2 space-y-0.5 text-xs">
                    <li
                      className={
                        passwordValidation.isLongEnough
                          ? "hidden"
                          : "text-red-600"
                      }
                    >
                      Must contain 12 letters
                    </li>
                    <li
                      className={
                        passwordValidation.hasUpperCase
                          ? "hidden"
                          : "text-red-600"
                      }
                    >
                      Must contain one Upper case
                    </li>
                    <li
                      className={
                        passwordValidation.hasLowerCase
                          ? "hidden"
                          : "text-red-600"
                      }
                    >
                      Must contain one Lower case
                    </li>
                    <li
                      className={
                        passwordValidation.hasNumber
                          ? "hidden"
                          : "text-red-600"
                      }
                    >
                      Must contain one Number
                    </li>
                    <li
                      className={
                        passwordValidation.hasSpecialChar
                          ? "hidden"
                          : "text-red-600"
                      }
                    >
                      Must contain one Special Character
                    </li>
                  </ul>
                )}
              </div>

              <div className="flex-grow flex flex-col justify-end space-y-3 pt-2">
                <button
                  type="submit"
                  disabled={isLoading}
                  className="w-full py-2.5 bg-[#5D56BD] text-white font-sans rounded-lg hover:bg-indigo-700 transition-colors"
                >
                  {isLoading ? "Creating account..." : "Create your account"}
                </button>

                <div className="relative py-2">
                  <div className="absolute inset-0 flex items-center">
                    <div className="w-full border-t border-gray-200"></div>
                  </div>
                  <div className="relative flex justify-center">
                    <span className="bg-white px-4 text-sm font-normal text-[#4D4D4D]">
                      Or
                    </span>
                  </div>
                </div>
                <GoogleSignIn/>


                <div className="text-center text-xs space-y-4">
                  <p>
                    Questions? Need help?{" "}
                    <a
                      href="#"
                      className="text-[#565DBD] hover:text-indigo-800"
                    >
                      Support can help
                    </a>
                  </p>
                  <div className="mt-4 mb-8 space-y-4">
                    Already have an account?{" "}
                    <Link
                      href="/auth/login"
                      className="text-indigo-600 hover:text-indigo-800"
                    >
                      Log In
                    </Link>
                  </div>
                  <div className="w-full h-[10px]"></div>
                </div>
              </div>
            </form>
          </div>
        </div>

        {/* Right Section */}
        <div className="w-1/2 h-full bg-indigo-50 flex items-center justify-center">
          <Image
            src={image1}
            alt="Illustration"
            className="w-4/5 h-auto"
            priority={true}
          />
        </div>
      </div>
    </>
  );
}
